$icon-with-text-icon-wrapper-width: 70px !default;
$icon-with-text-icon-wrapper-bg-color: transparent !default;

.icon-with-text {
  $this: &;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__icon-wrapper {
    width: $icon-with-text-icon-wrapper-width;
    margin: 0 auto 1.4rem auto;
    position: relative;
    z-index: 100;

    svg, img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      position: absolute;
      top: 0;
      left: 0
    }
  }

  &__title {
    @include font-size(lg);
    text-align: center;
    margin-bottom: 0.3rem;
  }

  &__text {
    text-align: center;
  }

  &--icon-circle {
    #{$this}__icon-wrapper {
      background-color: $icon-with-text-icon-wrapper-bg-color;
      height: $icon-with-text-icon-wrapper-width;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0;
      border-radius: 50%;

      svg, img {
        position: relative;
      }
    }
  }
}