.steps-list {
  margin-top: 2.5rem;

  @include for-size(tablet-down) {
    margin-top: 1.5rem;
  }

  &__list {
    width: calc(100% - 22.5rem);
    margin: 7.25rem auto 0;
    list-style: none;
    counter-reset: item-counter;

    @include for-size(tablet-down) {
      width: 90vw;
      margin-top: 8.5rem;
    }

    @include for-size(tablet-down) {
      margin-top: 6rem;
    }
  }

  &__item {
    display: flex;
    counter-increment: item-counter;
    width: 50%;

    @include for-size(tablet-down) {
      width: 75%;
      margin-bottom: 3rem;
    }

    &:nth-child(even) {
      margin-left: auto;
    }

    &:before {
      content: "0" counter(item-counter);
      color: var(--color-orange);
      font-size: 5.9rem;
      line-height: 9rem;
      padding-right: 1rem;

      @include for-size(tablet-down) {
        font-size: 2.8rem;
        padding-right: 0.5rem;
        line-height: 5rem;
      }

      @include for-size(phone-only) {
        line-height: 4.3rem;
      }
    }

    &-text {
      font-size: 1.25rem;
      margin: 1.56rem 0 0 calc(2rem + 3px);

      @include for-size(tablet-down) {
        margin: 1rem 0 0 1rem;
      }

      @include for-size(phone-only) {
        font-size: 0.875rem;
        margin-top: 0.75rem;
      }

      &-wapper {
        padding: 3rem 0 0 2rem;
      }
    }

    &-title {
      font-weight: 700;
      line-height: 1em;
      display: flex;
      align-items: self-end;

      &:before {
        content: "";
        border-left: 3px solid var(--color-blue-700);
        margin-right: 2rem;
        height: 9rem;
        display: flex;
      }

      @include for-size(tablet-down) {
        font-size: 2rem;

        &::before {
          height: 5rem;
          margin-right: 1rem;
          border-left: 1.5px solid var(--color-blue-700);
        }
      }

      @include for-size(phone-only) {
        font-size: 1.5rem;

        &::before {
          height: 4.3rem;
        }
      }
    }
  }
}
