.media {
  $this: &;
  display: block;
  position: relative;
  overflow: hidden;

  &--preserve-aspect-ratio {
    &::before {
      content: '';
      display: block;
      padding-top: var(--media-aspect-ratio);
    }

    #{$this} {
      &__object {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
      }
    }
  }

  &__object {
    $media-object: &;
    width: 100%;

    iframe {
      width: 100%;
    }

    &--fit-contain {
      width: 100%;
      height: 100%;
      object-fit: contain;
      position: absolute;
      top: 0;
      left: 0;
    }

    &--fit-cover {
      object-fit: cover;
      height: 100%;

      #{$media-object}-video-player {
        height: 100%;
        object-fit: cover;
      }
    }
  }
}