$rich-text-strong-weight: 900 !default;

.rich-text {
  strong {
    font-weight: $rich-text-strong-weight;
  }
  
  em {
    font-style: italic;
  }
  
  h1, h2, h3, h4, h5, h6 {
    @include heading-font;
    font-weight: 600;
    margin-bottom: 0.5rem;
  }
  
  h1 {
    @include font-size(xxl);
  }
  
  h2 {
    @include font-size(xl);
  }
  
  p {
    margin: 0 0 1rem 0;

    &:last-of-type {
      margin: 0;
    }
  }
  
  blockquote {
    font-style: italic;
    padding: 0 4rem;
  }
  
  img {
    max-width: 100%;
    height: auto;
  }
  
  a {
    color: $primary-color;
    text-decoration: underline;
    
    &:hover {
      opacity: 0.8;
    }
  }
  
  ul {
    list-style-type: disc;

    ul {
      list-style-type: circle;
    }
  }
  
  ol, ul {
    list-style-position: outside;
    padding-left: 1em;
    margin-bottom: 2rem;
    
    @include for-size(laptop-down) {
      margin-bottom: 1.75rem;
    }
    
    @include for-size(tablet-down) {
      margin-bottom: 1.25rem;
    }
  }

  iframe {
    max-width: 100%;
  }
}