$play-button-icon: url('assets/images/play.svg') !default;
$video-play-img: null !default;

.video {
  position: relative;

  &__unmute-button {
    @include font-size(xs);
    margin: 1em auto;
    max-width: calc(100% - 12.5em);
    min-height: 30px;
    padding: .8em;
    border-radius: 2px;
    background: white;
    color: black;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    cursor: pointer;
    opacity: 1;

    @include for-size(phone-only) {
      display: none;
    }

    &.hidden {
      opacity: 0;
      pointer-events: none;
    }

    &:hover {
      opacity: 0.75;
    }

    &-icon {
      width: 20px;
      margin-right: 5px;
    }
  }

  &__play-button {
    position: absolute;
    z-index: 200;
    width: 100%;
    height: 100%;

    &-icon {
      position: absolute;
      z-index: 200;
      width: 48px;
      height: 48px;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      background: $play-button-icon no-repeat;
      background-size: 48px 48px;
      opacity: 0.87;
      filter: drop-shadow(0 0 8px rgba(0,0,0,.12));
    }

    &:hover &-icon {
      opacity: 1;
    }
  }

  &--playing &__play-button {
    display: none;
  }

  &__poster {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    z-index: 100;
  }

  &__placeholder {
    &-button {
      position: absolute;
      z-index: 100;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;

      @if $video-play-img {
        &:after {
          content: '';
          left: 50%;
          top: 50%;
          width: 5rem;
          height: 5rem;
          background: url($video-play-img) no-repeat center;
          transform: translate(-50%, -50%);
        }
      }
    }

    &-image {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__player {
    width: 100%;
    display: block;
    outline: none;

    &-iframe {
      &--youtube {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}