.content-group {
  $cg: &;
  margin: 0 auto 1.125rem;
  padding: 0 12.875rem 0 11.875rem;

  @include for-size(tablet-down) {
    width: 90vw;
    padding: 0;
    flex-direction: row-reverse;
  }

  &__text-wrapper {
    width: 49.6vw;
    max-width: 100%;

    @include for-size(tablet-down) {
      width: 50%;
      display: flex;
    }
  }

  &__content-wrapper {
    width: 21.18vw;

    @include for-size(tablet-down) {
      width: 50%;
      display: block;
      margin: auto;
    }
  }

  &__text {
    @include font-size(xl);

    @include for-size(tablet-down) {
      @include font-size(lg);
    }
  }

  &__button {
    margin-top: 1.5rem;

    @include for-size(tablet-down) {
      white-space: nowrap;
      margin-left: 0;
    }
  }

  &--image-on-top {
    flex-direction: column;

    #{$cg}__text-wrapper {
      order: 2;
    }

    #{$cg}__content-wrapper {
      order: 1;
    }
  }
}
