.logos-grid {
  margin: 3.5rem auto 8rem;

  @include for-size(tablet-down) {
    margin: 3rem auto;
  }

  @include for-size(phone-only) {
    margin: 0.5rem auto 1rem;
  }

  &__list {
    width: 75vw;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    row-gap: 4.6rem;
    column-gap: 6rem;
    margin: 6rem auto 0;

    @include for-size(tablet-down) {
      margin: 2rem auto 0;
      width: 90vw;
      column-gap: 3em;
      row-gap: 3rem;
    }

    @include for-size(phone-only) {
      width: 90vw;
      grid-template-columns: 1fr 1fr;
      column-gap: 3.9rem;
    }
  }

  &__media {
    width: 100%;
    height: 4rem;

    @include for-size(phone-only) {
      height: 3rem;
    }
  }
}
