.heading {
  &__content {
    width: calc(100% - 6.25rem);
    margin: auto;
    position: relative;

    @include for-size(phone-only) {
      width: calc(100% - 10vw);
    }
  }

  &__title {
    font-size: 13.88vw;
    line-height: 1em;
    color: rgba(2, 121, 147, 0.2);
    text-transform: uppercase;
    font-weight: 700;
    opacity: 0.4;
  }

  &__text {
    font-size: 3.472vw;
    font-weight: 700;
    position: absolute;
    left: 7%;
    top: 46.25%;
    max-width: 77vw;

    @include for-size(phone-only) {
      font-size: 5.34vw;
    }
  }
}
