.field {
  &__input {
    @include font-size(sm);
    width: 100%;
    padding: 0.8rem;
    margin-bottom: 0.875rem;
    border: none;
    background-color: white;

    &--text,
    &--email {
      height: 2.25rem;

      @media (max-width: 950px) {
        height: 2.85rem;
      }
    }

    &--checkbox {
      width: 1rem;
      height: 1rem;
      margin-bottom: 0;
    }

    &--invalid {
      border: 1px solid var(--color-orange);
      margin-bottom: 0.3rem;
    }
  }

  &--checkbox {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 1.6rem;

    .field__input {
      display: none;

      & + .field__label {
        @include font-size(sm);

        &:before {
          content: "";
          width: 20px;
          height: 19px;
          border: 1px solid var(--color-orange);
          background-color: #f4f9fe;
          vertical-align: middle;
          display: inline-block;
          margin-right: 0.6rem;
          transition: background-color 350ms;
        }
      }

      &:checked {
        & + .field__label:before {
          background-color: var(--color-orange);
        }
      }
    }

    .field__error {
      margin-top: 0.3rem;
    }
  }

  &--textarea textarea {
    resize: none;
    margin: 0;
  }

  &__error {
    @include font-size(xs);
    width: 100%;
    color: var(--color-orange);
    margin-bottom: 0.5rem;
    display: block;
  }
}
