html {
  @include font-size(md);
  background: var(--sj-black);
  color: var(--sj-white);
}

body {
  @include primary-font;
}

.site {
  &--not-scrolling,
  &--nav-opened {
    overflow: hidden;

    ::-webkit-scrollbar {
      display: none;
    }
  }

  &__main {
    margin-top: var(--header-height);
  }

  &-header {
    $header: &;
    width: 100%;
    background-color: white;
    display: flex;
    justify-content: space-between;
    padding-left: 3.125rem;
    height: var(--header-height);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;

    @include for-size(tablet-down) {
      padding: 0 3.125rem;
    }

    @include for-size(phone-only) {
      padding: 0 5vw;
    }

    &__logo {
      width: 12.5rem;

      @include for-size(tablet-down) {
        display: flex;
        align-items: center;
        width: 8rem;
      }

      &-img {
        display: block;
      }

      svg {
        width: 100%;
      }
    }

    &__main-nav {
      @include for-size(tablet-down) {
        width: 100%;
        height: calc(100% - 4.13rem);
        background-color: white;
        z-index: 100;
        position: fixed;
        right: 0;
        top: 4.13rem;
        transform: translatex(100%);
        transition: transform 250ms;
      }

      &--opened {
        transform: translatex(0);
        transition: transform 250ms;
      }
    }

    &__main-menu {
      display: flex;
      align-items: center;
      height: 100%;

      @include for-size(tablet-down) {
        flex-direction: column;
        justify-content: center;
      }

      .menu-item {
        @include font-size(lg);
        font-weight: 500;
        margin-left: 1.875rem;
        height: 100%;
        display: flex;
        align-items: center;

        @include for-size(tablet-down) {
          font-size: 2rem;
          height: auto;
          margin: 0 0 4rem 0;
        }

        &.button > a {
          height: 100%;
        }

        &:not(.button) > a {
          &:hover {
            color: var(--color-orange);
          }
        }
      }
    }
  }

  &-footer {
    @include font-size(sm);
    font-weight: 400;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2.875rem 0 4.3rem;
    height: var(--footer-height);
    position: relative;

    @include for-size(phone-only) {
      padding: 0 5vw 0 5vw;
    }

    &__social-networks-menu {
      font-size: 0;
      display: flex;
      position: absolute;
      right: 50%;
      top: 50%;
      transform: translate(50%, -50%);

      .menu-item {
        width: 20px;
        height: 20px;
        background-color: black;
        -webkit-mask-repeat: no-repeat;
        mask-repeat: no-repeat;
        -webkit-mask-position: left center;
        mask-position: left center;
        -webkit-mask-size: contain;
        mask-size: contain;

        &:hover {
          opacity: 0.7;
        }

        &--linkedin {
          -webkit-mask-image: url("assets/images/icon-linkedin.svg");
          mask-image: url("assets/images/icon-linkedin.svg");
        }

        & > a {
          display: block;
          height: 100%;
        }
      }
    }

    &__legal-menu {
      display: flex;

      .menu-item {
        &:not(:last-child) {
          &:after {
            content: "|";
            margin: 0 0.5rem;
          }
        }

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}
