.content-group-overlapped {
  margin-top: -44.6rem;
  z-index: 10;
  position: relative;
  display: flex;

  @include for-size(tablet-down) {
    margin-top: -18rem;
  }

  &:before {
    content: "";
    background-color: white;
    height: 1rem;
    width: 30vw;
    position: absolute;
    right: 0;
    bottom: 30.5rem;

    @include for-size(tablet-down) {
      bottom: 40rem;
      height: 0.31rem;
    }
  }

  &:after {
    content: "";
    background-color: white;
    height: 1rem;
    width: 20vw;
    position: absolute;
    right: 0;
    bottom: 14.78rem;

    @include for-size(tablet-down) {
      bottom: 33rem;
      height: 0.31rem;
    }
  }

  &__background {
    width: 100%;
    height: 100%;
    min-height: 45rem;
    position: relative;
    overflow: hidden;

    @include for-size(phone-only) {
      min-height: 42rem;
    }

    &-triangle {
      width: 29vw;
      height: calc(100% + 1.2rem);
      position: absolute;
      right: 0;
      top: -1.2rem;

      @include for-size(tablet-down) {
        width: 25%;
      }

      &::after {
        content: "";
        width: 0;
        height: 0;
        border-bottom: 45rem solid white;
        border-right: calc(29vw + 1px) solid transparent;

        @include for-size(tablet-down) {
          border-right: calc(30vw) solid transparent;
          border-bottom: 20rem solid white;
        }
      }
    }

    &:before {
      content: "";
      width: 71%;
      height: 100%;
      background-color: white;
      position: absolute;
      left: 0;
      top: 0;

      @include for-size(tablet-down) {
        width: 75%;
      }
    }
  }

  &__content {
    padding: 4rem 12.5rem 0 9.37rem;
    margin-bottom: 2rem;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 100;

    @media (max-width: 1220px) {
      padding: 2rem 10rem 0 5vw;
      margin-bottom: 0;
    }
  }

  &__title {
    font-weight: 700;
    margin-bottom: 3.75rem;

    @include for-size(tablet-down) {
      font-size: 2.5rem;
    }

    @include for-size(phone-only) {
      font-size: 1.31rem;
      margin-bottom: 1.6rem;
    }
  }

  &__text {
    font-size: 1.875rem;

    @include for-size(tablet-down) {
      font-size: 1.3rem;
    }

    @include for-size(phone-only) {
      font-size: 1rem;
    }

    ul {
      list-style: none;
      padding: 0;
    }

    li {
      margin-bottom: 2.75rem;
      display: flex;
      align-items: center;

      @include for-size(tablet-down) {
        margin-bottom: 1.3rem;
      }

      &::before {
        content: "";
        background-color: var(--color-blue-700);
        -webkit-mask-repeat: no-repeat;
        mask-repeat: no-repeat;
        -webkit-mask-position: left center;
        mask-position: left center;
        -webkit-mask-size: contain;
        mask-size: contain;
        -webkit-mask-image: url("assets/images/list-item-decoration.svg");
        mask-image: url("assets/images/list-item-decoration.svg");
        width: 25.66px;
        height: 25px;
        display: inline-block;
        padding-right: 2.25rem;

        @include for-size(phone-only) {
          width: 12.5px;
          height: 12.5px;
          padding-right: 1rem;
        }
      }

      &:nth-child(even) {
        &::before {
          background-color: var(--color-orange);
        }
      }
    }

    &-wrapper {
      width: 57.36vw;

      @include for-size(tablet-down) {
        width: 65vw;
      }
    }
  }

  &__image {
    width: 34.75rem;
    position: absolute;
    right: 13.125rem;
    bottom: 1.5rem;

    @media (max-width: 1220px) {
      width: 39vw;
    }

    @include for-size(tablet-down) {
      right: 50%;
      transform: translateX(50%);
    }

    @include for-size(phone-only) {
      width: 75vw;
      bottom: 2rem;
    }

    @media (min-width: 1600px) {
      right: 26rem;
    }
  }
}
