.contact-page {
  &__content {
    display: flex;
    padding: 3.43rem 0 4.375rem 4.375rem;
    min-height: 90vh;

    @include for-size(tablet-down) {
      flex-direction: column;
    }

    @include for-size(tablet-down) {
      padding: 0 5vw;
    }
  }

  &__left-column {
    width: 44.75vw;
    display: flex;
    align-items: center;
    flex: 1;
    position: relative;

    @include for-size(tablet-down) {
      height: 20vh;
      width: 100%;
      overflow: hidden;
      flex: initial;
    }

    @include for-size(phone-only) {
      width: 100%;
      height: 5.94rem;
      overflow: hidden;
      flex: initial;
    }
  }

  &__background-video {
    height: calc(100% - 2.8rem);

    @include for-size(tablet-down) {
      width: 100%;
      height: auto;

      .video {
        min-width: 100%;
      }
    }
  }

  &__logo {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    @include for-size(phone-only) {
      display: none;
    }
  }

  &__right-column {
    width: 56.25vw;
    background-color: var(--color-gray-400);
    padding: 3.75rem 5.625rem 3.75rem 5.375rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    @include for-size(tablet-down) {
      width: 100%;
      padding: 1.9rem 2.375rem 2.56rem;
    }

    .contact-form__message-title {
      font-size: 1.8rem;
      line-height: 2em;
    }
  }

  &__form {
    width: 100%;
    align-self: baseline;

    &-text {
      width: 33vw;
      margin-bottom: 2.8rem;
      align-self: flex-end;

      @include for-size(tablet-down) {
        width: 100%;
        margin-bottom: 1.5rem;
      }

      h1 {
        font-size: 2.19rem;
        font-weight: 700;
        margin-bottom: 0.5rem;

        @include for-size(tablet-down) {
          font-size: 1.9rem;
        }

        @include for-size(phone-only) {
          font-size: 1.25rem;
        }
      }

      p {
        line-height: 1.5rem;

        @include for-size(phone-only) {
          font-size: 0.875rem;
        }
      }
    }

    &-field-group {
      width: calc(50% - 7px);

      @media (max-width: 950px) {
        width: 100%;
      }
    }

    &-group {
      display: flex;
      justify-content: space-between;

      @media (max-width: 950px) {
        flex-direction: column;
      }

      .field {
        width: 100%;
      }
    }

    &-submit {
      @include font-size(lg);
      display: flex;

      @include for-size(tablet-down) {
        margin-right: auto;
      }

      @include for-size(phone-only) {
        justify-content: flex-start;
      }
    }

    &-terms-and-conditions {
      margin-bottom: 1.687rem;

      @media (max-width: 1150px) {
        margin-bottom: 1.5rem;
      }
    }

    .contact-form__message--error {
      @include font-size(sm);
      color: var(--color-orange);
      margin-top: 1rem;

      .title {
        @include font-size(md);
      }
    }
  }

  &__label {
    margin-bottom: 0.3rem;
    display: block;

    @include for-size(phone-only) {
      font-size: 0.75rem;
    }
  }
}
