.content-group {
  display: flex;
  justify-content: space-between;
  position: relative;
  margin: 9rem auto;

  @include for-size(tablet-down) {
    flex-direction: column;
    margin: 5rem 0;
  }

  &__title {
    display: block;
    margin-bottom: 0.54rem;
  }

  &__text {
    &-wrapper {
      width: 50%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      position: relative;
      z-index: 100;
      margin-right: 2rem;

      @include for-size(desktop-up) {
        max-width: 40%;
        margin-right: 0;
      }

      @include for-size(tablet-down) {
        width: 100%;
        margin: 2rem 0;
        display: block;
        order: 2;
      }
    }

    > p:last-of-type {
      margin-bottom: 0;
    }
  }

  &__button {
    margin-top: 1.36rem;

    @include for-size(tablet-down) {
      margin: 1.36rem auto 0 auto;
      display: block;
      text-align: center;
    }
  }

  &__content-wrapper {
    width: 50%;
    position: relative;
    display: flex;
    align-items: center;

    img {
      max-width: 100%;
    }

    @include for-size(tablet-down) {
      width: 100%;

      img {
        width: 100%;
      }
    }
  }
  
  @include for-size(tablet-up) {
    &--normal {
      .content-group__text-wrapper {
        margin-right: 3.6rem; 
      }
    }

    &--swap {
      .content-group__content-wrapper {
        order: 1;
      }

      .content-group__text-wrapper {
        order: 2;
        margin-left: 3.6rem;

        @include for-size(tablet-down) {
          margin: 2rem 0 0 0;
        }
      }
    }
  }
}