.content-group-list {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 9.8rem;

  @include for-size(tablet-down) {
    margin: 1rem auto 2rem;
  }

  &__items {
    padding-right: 11.25rem;
    margin: 6rem auto 0;

    @include for-size(tablet-down) {
      width: 100vw;
      padding: 0;
      margin: 2.5rem auto 0;
    }
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 3.68rem;

    @include for-size(tablet-down) {
      flex-direction: column;
    }

    &::before {
      content: "";
      background-image: url("assets/images/content-group-list-divisor.svg?2023031602");
      background-repeat: no-repeat;
      background-size: 100% auto;
      width: 100%;
      aspect-ratio: 1261 / 69;
      display: block;
    }

    @include for-size(phone-only) {
      overflow: hidden;
      position: relative;
      padding-top: 1rem;

      &:before {
        width: 195%;
        position: absolute;
        top: 0;
      }
    }

    &-image {
      width: 55%;

      @include for-size(tablet-down) {
        width: 100%;
      }
    }

    &-text {
      padding: 0 1.6rem 0 11.5rem;
      margin-top: -2.75rem;
      flex: 1;

      @include for-size(tablet-down) {
        width: 71vw;
        margin: 0 auto 1rem;
        padding: 0;
      }

      h2 {
        font-size: 2.18rem;
        font-weight: 700;
        margin-bottom: 1rem;

        @include for-size(tablet-down) {
          font-size: 2rem;
        }

        @include for-size(phone-only) {
          font-size: 1.5rem;
          margin-bottom: 0.5rem;
        }
      }

      p {
        font-size: 1.25rem;

        @include for-size(phone-only) {
          font-size: 0.875rem;
        }
      }
    }
  }

  &__button {
    margin: auto;
  }
}
