.hero {
  $hero: &;
  position: relative;
  color: white;

  &--with-paddings {
    padding: 0 3.125rem;
    height: calc(100vh - var(--header-height) - var(--footer-height));

    @include for-size(phone-only) {
      padding: 0 5vw;
    }

    #{$hero}__main-text {
      font-size: 3.5rem;
      width: 59vw;
      top: 3.875rem;
      left: 6.5rem;

      @include for-size(tablet-down) {
        font-size: 2.5rem;
      }

      @include for-size(phone-only) {
        font-size: 1.8rem;
        width: calc(100vw - 14vw);
        left: 7vw;
      }
    }
  }

  &--100vw {
    height: 72.75rem;
    z-index: 1;

    @include for-size(tablet-down) {
      height: 30rem;
    }

    #{$hero}__main-text {
      max-width: 85.55vw;
      font-size: 3.75rem;
      line-height: 3.67rem;
      top: 3.44rem;
      left: 3.37rem;

      @include for-size(tablet-down) {
        font-size: 2.2rem;
        line-height: 1em;
      }

      @include for-size(phone-only) {
        font-size: 1rem;
        width: 90%;
        max-width: 90%;
        left: 5vw;
      }

      strong {
        font-size: 5.9rem;
        line-height: 5.8rem;
        display: block;

        @include for-size(tablet-down) {
          font-size: 2.5rem;
          line-height: 1em;
        }

        @include for-size(phone-only) {
          font-size: 1.68rem;
        }

        @include for-size(laptop-up) {
          &.left-aligned {
            text-align: right;
          }
        }
      }
    }
  }

  &__media {
    height: 100%;
  }

  &__main-text {
    position: absolute;
    text-transform: uppercase;
  }

  &__small-text {
    font-size: 1.7rem;
    position: absolute;
    bottom: 15.69rem;
    right: 8rem;

    @include for-size(tablet-down) {
      @include font-size(lg);
    }

    @include for-size(phone-only) {
      @include font-size(md);
      bottom: auto;
      top: 45vh;
      right: 10vw;
    }
  }
}
