.portfolio-page {
  &__numbered-list {
    margin-top: 7rem;

    @include for-size(tablet-down) {
      margin-top: 2.7rem;
      margin-bottom: 2rem;
    }

    &-item {
      scroll-snap-align: center;
      scroll-snap-stop: always;
    }

    &-item,
    &-popup-heading-wrapper {
      display: flex;
      justify-content: flex-end;
      margin: 2rem 13rem 7rem
        calc(var(--portfolio-navigation-line-width) + 10rem);
      padding-bottom: 2.81rem;
      position: relative;

      @include for-size(tablet-down) {
        flex-direction: column;
        margin: 0;
        padding: 2rem 3.18rem 0
          calc(var(--portfolio-navigation-line-width) + 7rem);
      }

      &:after {
        content: "";
        background: url(assets/images/portfolio-divisor.svg);
        background-repeat: no-repeat;
        background-size: contain;
        width: 100%;
        aspect-ratio: 6;
        position: absolute;
        bottom: 0;
        right: 0;

        @include for-size(tablet-down) {
          background-image: url(assets/images/portfolio-divisor-mobile.svg);
          aspect-ratio: 4.5;
          width: calc(100vw - 5.375rem);
          right: initial;
          left: 0;
        }
      }

      &-pretitle {
        text-transform: uppercase;
        line-height: 1.5em;
        display: block;
        margin-bottom: 0.75rem;

        @include for-size(phone-only) {
          font-size: 0.6rem;
        }
      }

      &-title {
        @include font-size(xxl);
        font-weight: 700;
        line-height: 1em;
        display: block;
        margin-bottom: 1rem;

        @include for-size(tablet-down) {
          font-size: 2.25rem;
        }

        @include for-size(phone-only) {
          font-size: 1.3rem;
        }

        &:before {
          left: -1.43rem;
          transform: translateX(-100%);

          @include for-size(phone-only) {
            transform: translateX(-35%);
            font-size: 1.75rem;
            bottom: -0.05em;
          }
        }
      }

      &-text {
        font-size: 1.25rem;
        line-height: 1.4em;
        padding-right: 2rem;
        margin-bottom: 2rem;

        @include for-size(phone-only) {
          font-size: 0.875rem;
          padding: 0;
        }

        &-wrapper {
          width: 44%;

          @include for-size(laptop-up) {
            padding-bottom: 17%;
          }

          @include for-size(tablet-down) {
            width: 100%;
          }
        }
      }

      &-image-wrapper {
        width: 46%;
        aspect-ratio: 1;
        flex-shrink: 0;

        @include for-size(tablet-down) {
          width: 100%;
          margin: 2rem 0 1rem;
          transform: translateX(-3rem);
        }
      }

      &-media {
        width: 100%;
        height: 100%;
      }

      &-button {
        font-size: 1.125rem;
        margin-bottom: 0.5rem;
      }
    }

    &-popup {
      &-heading-wrapper {
        padding-right: calc(13rem - 5vw);
        margin: 0;

        @include for-size(tablet-down) {
          padding-left: 22vw;
          padding-right: 5vw;
        }

        &:after {
          background-image: url(assets/images/portfolio-popup-divisor.svg);
          aspect-ratio: 7;

          @include for-size(tablet-down) {
            background-image: url(assets/images/portfolio-popup-divisor-mobile.svg);
            aspect-ratio: 5;
            width: 100%;
          }
        }

        &.info-empty {
          margin-bottom: 8rem;
        }
      }

      &-categories {
        display: grid;
        grid-template-columns: 44% 46%;
        grid-gap: 6%;

        &-title {
          font-size: 2.18rem;
          font-weight: 500;
          text-transform: uppercase;

          &-wrapper {
            display: flex;
            justify-content: flex-end;
            padding: 1.5rem;
            transform: translateY(
              calc(
                -1 * (
                    100vw - var(--portfolio-navigation-line-width) - 10rem - 5vw
                  ) / 7
              )
            );
          }
        }

        &-list {
          &-item {
            @include for-size(tablet-up) {
              @include font-size(lg);
            }

            display: inline-block;
            margin-top: 1rem;
            margin-right: 1.5rem;
          }
        }
      }

      &-heading-content {
        @include for-size(laptop-up) {
          margin-bottom: 1rem;
        }
      }

      &-info-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;

        .heading__content {
          width: auto;
        }
      }

      &-content-group {
        $cg: &;
        width: 76%;
        margin: 0 5.375rem 5.25rem auto;
        padding: 0;
        flex-direction: row;

        &-text {
          font-size: 1.875rem;
          line-height: 1.3em;

          strong {
            font-weight: 700;
          }

          &-wrapper {
            width: 100%;
            margin-right: 3.75rem;
            align-self: flex-start;
          }
        }

        &-content-wrapper {
          width: 80%;
          align-self: flex-start;
          margin-bottom: 2.8rem;

          .media {
            width: 100%;
          }
        }

        &.content-group--image-on-top {
          flex-direction: column;
          margin: 0 auto 4.75rem;

          #{$cg}-text-wrapper {
            padding-right: 4.3rem;
            margin: 0;
          }
        }
      }

      &-footer {
        position: fixed;
        z-index: 100;
        width: 100%;
        left: 0;

        @include for-size(laptop-up) {
          transform: translateY(130%);
        }

        @include for-size(phone-only) {
          transform: translateY(65%);
        }
      }

      @include for-size(tablet-down) {
        .portfolio-page__numbered-list {
          &-popup {
            &-heading-wrapper {
              padding-bottom: 1rem;

              .numbered-list__item-image-wrapper {
                width: calc(95vw * 0.6);
                align-self: flex-end;
                margin: 0;
                transform: translateX(0);
              }
            }

            &-categories {
              grid-template-columns: 30% 60%;
              grid-gap: 10%;
              margin-bottom: 4.375rem;

              &-title {
                font-size: 4.5vw;

                &-wrapper {
                  justify-content: flex-start;
                  padding: 0 0 0 1rem;
                  transform: translateY(-15vw);
                }
              }

              &-list-item {
                margin-right: 1rem;
              }
            }

            &-content {
              &-group {
                width: 77vw;
                margin-left: 13vw;
                margin-bottom: 2rem;
                flex-direction: column;

                &-content {
                  &-wrapper {
                    order: 2;
                  }
                }

                &.content-group--image-on-top {
                  .content-group__content-wrapper {
                    order: 1;
                  }
                }

                &-text {
                  font-size: 1.5rem;
                  line-height: 1.21em;

                  &-wrapper {
                    padding-right: 0 !important;
                  }
                }

                &-button {
                  display: inline-flex;
                }
              }
            }
          }
        }
      }

      @include for-size(phone-only) {
        .portfolio-page__numbered-list {
          &-popup-content-group {
            &-text {
              font-size: 0.875rem;
            }
          }
        }
      }
    }
  }
}
