$title-color: inherit !default;

.title {
  @include heading-font;
  @include font-size(xl);
  color: $title-color;
  display: block;

  &--xxxl {
    @include font-size(xxxl);
  }

  &--xxl {
    @include font-size(xxl);
  }

  &--lg {
    @include font-size(lg);
  }

  &--md {
    @include font-size(md);
  }

  &--sm {
    @include font-size(sm);
  }

  &--xs {
    @include font-size(xs);
  }
}