@import '~flickity/css/flickity';

$slider-arrow-img: "" !default;

.slider {
  width: 100%;
  position: relative;  
  margin: 0 auto;
  outline: none;

  &__arrow {
    font-size: 0;
    display: inline-block;
    width: 1.82rem;
    height: 1.82rem;
    position: absolute;
    top: 50%;
    z-index: 100;

    &.previous {
      left: -2rem;
      transform: translateY(-50%) rotate(180deg);
    }

    &.next {
      right: -2rem;
      transform: translateY(-50%);
    }

    &:not(:disabled):hover {
      opacity: 0.7;
    }

    &:focus {
      box-shadow: none;
    }
  }

  &__slides {
    margin: auto;
    position: relative;

    .flickity-viewport {
      width: 100%;
      overflow: hidden;
    }

    .flickity-button {
      background-color: transparent;
      background-image: $slider-arrow-img;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;

      &:disabled {
        pointer-events: auto;
      }
    }
  }

  &__slide {
    display: inline-block;
    vertical-align: top;
    position: relative;
    outline: none;
  }

  &__dots {
    bottom: 0.5rem;

    .dot {
      font-size: 0;
      width: 16px;
      height: 16px;
      margin: 0 0.5rem;
      border: 1px solid white;
      border-radius: 50%;
      position: relative;
      box-sizing: border-box;
      background: transparent;
      cursor: pointer;

      &.is-selected:before {
        content: '';
        width: 10px;
        height: 10px;
        background-color: white;
        border-radius: 50%;
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }

      &:hover {
        opacity: 0.7;
      }
    }
  }
}