.numbered-list {
  list-style: none;
  counter-reset: item-counter;
  position: relative;

  &__item {
    counter-increment: item-counter;
    margin-bottom: 3.375rem;

    @media (max-width: 950px) {
      margin-bottom: 6.5rem;
    }

    @include for-size(tablet-down) {
      margin-bottom: 4.2rem;
    }

    &-title {
      position: relative;

      &:before {
        content: "0" counter(item-counter);
        color: var(--color-orange);
        font-weight: 500;

        line-height: 1em;
        position: absolute;
        left: 0;
      }

      &:after {
        margin-left: 1.5rem;
      }
    }
  }

  &__navigation {
    position: relative;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include for-size(tablet-up) {
      position: sticky;
      top: calc(5rem + var(--header-height));
      left: 0;
      flex-direction: column;
    }

    @include for-size(tablet-down) {
      padding: 0 2rem;
      position: sticky;
      top: 8rem;
      left: 0;
      transform: translateY(-50%);
    }

    &-wrapper {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;

      @include for-size(laptop-up) {
        transform: translateX(var(--portfolio-navigation-line-width));
      }

      @include for-size(phone-only) {
        transform: translateX(0%);
        width: 100%;
        pointer-events: none;
      }
    }

    &-list {
      &-item {
        $nav-item: &;
        color: black;
        font-size: 2.1875rem;
        font-weight: 500;
        position: relative;

        @include for-size(tablet-up) {
          margin-bottom: 2rem;
        }

        @include for-size(phone-only) {
          margin-bottom: 1rem;
        }

        &--active {
          #{$nav-item}-button {
            opacity: 0;
            pointer-events: none;
            transition: opacity 350ms;
          }

          #{$nav-item}-label {
            opacity: 1;
            transition: opacity 1100ms 100ms;

            @include for-size(phone-only) {
              font-size: 1rem;
            }
          }
        }

        &:hover {
          color: var(--color-orange);
        }

        &-button {
          opacity: 1;
          cursor: pointer;
          transition: opacity 350ms;

          @include for-size(phone-only) {
            pointer-events: none;
            margin-left: 1rem;
            font-size: inherit;
          }
        }

        &-label {
          font-weight: 500;
          display: flex;
          align-items: center;
          position: absolute;
          white-space: nowrap;
          top: 0;
          left: 0;
          opacity: 0;
          pointer-events: none;
          color: var(--color-orange);
          transition: opacity 500ms;

          &:before {
            content: "";
            width: var(--portfolio-navigation-line-width);
            height: 2px;
            background-color: var(--color-blue-700);
            display: inline-block;
            margin-right: 1rem;
            margin-left: calc(
              -1 * var(--portfolio-navigation-line-width) - 1rem
            );
          }

          @include for-size(phone-only) {
            &:before {
              --portfolio-navigation-line-width: 10vw;
              margin-left: calc(-1 * var(--portfolio-navigation-line-width));
            }
          }
        }

        @include for-size(phone-only) {
          font-size: 1rem;
        }
      }

      @include for-size(phone-only) {
        position: relative;
        &::before {
          content: "";
          position: absolute;
          left: 75%;
          top: 50%;
          width: 0;
          height: 0;
          transform: translate(-50%, -50%);
          border-radius: 100%;
          box-shadow: 0 0 2em 4em white;
        }
      }
    }
  }

  &__popup {
    --numbered-list-popup-triangle-height: calc(5vw / 0.4478);
    --numbered-list-popup-footer-height: calc(
      0.486486 * (var(--portfolio-navigation-line-width) + 10rem)
    );
    background-color: transparent;
    height: auto;
    min-height: 100vh;
    align-items: flex-end;
    justify-content: flex-end;
    padding: 0 0 var(--numbered-list-popup-footer-height) 0;

    .popup__body {
      background: transparent;
      height: 100vh;
      flex-grow: 1;
      padding: calc(
          var(--numbered-list-popup-triangle-height) +
            var(--numbered-list-popup-offset, 0px)
        )
        5vw 0 calc(var(--portfolio-navigation-line-width) + 10rem);
      border-radius: 0;
      position: relative;
      overflow-y: scroll;
      box-shadow: none;

      @include for-size(tablet-down) {
        padding: calc(
            var(--numbered-list-popup-triangle-height) +
              var(--numbered-list-popup-offset, 0px)
          )
          5vw 0 0;
      }

      &:before {
        content: "";
        background-image: url(assets/images/portfolio-popup-top-decoration.svg);
        background-repeat: no-repeat;
        width: 5vw;
        aspect-ratio: 0.4478;
        position: absolute;
        top: var(--numbered-list-popup-offset, 0px);
        right: 0;
        background-position: left bottom;
      }

      &-content {
        background-color: var(--color-blue-100);
        position: relative;

        &:before {
          content: "";
          width: 5vw;
          height: calc(100% + var(--numbered-list-popup-footer-height));
          background-color: var(--color-blue-400);
          position: absolute;
          top: 0;
          right: 0;
          transform: translateX(100%);
        }

        &:after {
          content: "";
          background-image: url(assets/images/portfolio-popup-bottom-decoration.svg);
          background-repeat: no-repeat;
          width: calc(var(--portfolio-navigation-line-width) + 10rem);
          aspect-ratio: 0.572;
          position: absolute;
          bottom: calc(
            -0.49 * (var(--portfolio-navigation-line-width) + 10rem)
          );
          left: 0;
          transform: translateX(
            calc(-1 * var(--portfolio-navigation-line-width) - 10rem)
          );

          @include for-size(tablet-down) {
            bottom: -20vw;
            transform: none;
            width: 35vw;
          }
        }

        &-wrapper {
          padding-top: 3.125rem;

          &:after {
            content: "";
            width: calc(100% + 2px);
            height: var(--numbered-list-popup-footer-height);
            background-color: var(--color-blue-400);
            position: absolute;
            bottom: 0;
            left: -1px;
            transform: translateY(100%);
          }
        }
      }
    }

    .popup__close-button {
      top: calc(
        var(--numbered-list-popup-triangle-height) +
          var(--numbered-list-popup-offset, 0px)
      );
      right: 5vw;
    }
  }
}
