$popup-z-index: 10000 !default;
$popup-overlay-color: rgba(black, 0.35) !default;
$popup-margin: 10 !default;
$popup-shadow: 0 2px 10px rgba(black, 0.25) !default;
$popup-background-color: white !default;
$popup-border-radius: 10px !default;
$popup-close-icon: null !default;
$popup-close-size: 3.5rem !default;
$popup-spinner: url('assets/images/spinner.svg') !default;
$popup-spinner-size: 6rem !default;

.popup {
  $this: &;
  background-color: $popup-overlay-color;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  z-index: $popup-z-index;
  opacity: 0;
  transition: opacity 250ms;
  overflow-y: scroll;
  padding: #{$popup-margin}vh #{$popup-margin}vw;

  &--opened {
    opacity: 1;
    transition-duration: 750ms;

    #{$this}__body {
      transform: translate3d(0, 0, 0);
    }
  }

  &--scrollable {
    align-items: flex-start;
  }

  &--no-margins {
    padding: 0;
  }

  &__body {
    position: relative;
    background-color: $popup-background-color;
    border-radius: $popup-border-radius;
    box-shadow: $popup-shadow;
    max-width: 100%;
    transform: translate3d(0, 10%, 0);
    transition: transform 500ms;

    @if ($popup-spinner) {
      &-content {
        &--loading {
          &:after {
            content: '';
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: $popup-spinner-size;
            height: $popup-spinner-size;
            background: $popup-spinner no-repeat center center;
            background-size: cover;
          }
        }
      }
    }
  }

  &__close-button {
    color: black;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    right: 0;
    transition: opacity 350ms;
    width: $popup-close-size;
    height: $popup-close-size;

    @if ($popup-close-icon) {
      background: $popup-close-icon no-repeat center;
      background-size: 1rem 1rem;
    } @else {
      font-size: 1.5rem;
    }

    &:hover {
      opacity: 0.65;
    }

    @if (not $popup-close-icon) {
      &:before {
        content: '\2715';
      }
    }
  }
}