.team-members {
  margin: 1.75rem auto 0;

  &__list {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-wrap: wrap;
    padding: 0 1.5rem;
    margin-top: 4.68rem;

    @include for-size(tablet-down) {
      padding: 0 5vw;
      margin-top: 3rem;
      justify-content: space-between;
    }

    &-item {
      $item: &;
      width: calc(33.3% - 14rem);
      padding-bottom: 1rem;
      margin: 0 7rem 3.75rem;
      text-align: center;
      position: relative;

      &-wrapper {
        transform: translate(0, 0);
        transition: transform 250ms;
      }

      @include for-size(tablet-down) {
        width: calc(50% - 5vw);
        margin: 0 0 3.5rem 0;
        padding-bottom: 0;

        &-wrapper {
          width: 100%;
        }
      }

      @include for-size(phone-only) {
        display: flex;
        justify-content: center;
        width: calc(50% - 0.75rem);
        margin: 0 0 2.5rem 0;
      }

      &:before {
        content: "";
        border-right: 2.3px solid var(--color-blue-700);
        border-bottom: 2.3px solid var(--color-blue-700);
        cursor: pointer;
        width: 100%;
        height: 100%;
        position: absolute;
        right: 0;
        bottom: 0;
        opacity: 0;
        transition: opacity 250ms;
      }

      &-group {
        position: relative;
        width: 12rem;
        margin: auto;

        @include for-size(phone-only) {
          width: 7rem;
        }
      }

      &-media {
        margin: auto;

        @include for-size(phone-only) {
          width: 7rem;
        }
      }

      &-button {
        position: absolute;
        right: 0;
        top: 50%;
        width: 2.5rem;
        height: 2.5rem;
        background-color: white;
        border-radius: 50%;
        box-shadow: 0px 4.60638px 4.60638px rgba(0, 0, 0, 0.25);
        opacity: 0;
        transform: translate(50%, -50%);

        &:after {
          content: "";
          background-image: url("assets/images/icon-open-popup.svg");
          background-size: 1.2rem;
          background-position: center;
          background-repeat: no-repeat;
          width: 1.23rem;
          height: 1.23rem;
          display: block;
          margin: auto;
        }
      }

      @include for-size(laptop-up) {
        &:hover {
          cursor: pointer;

          &#{$item}--no-action {
            cursor: default;
          }

          &:before {
            opacity: 1;
          }

          #{$item}-wrapper {
            transform: translate(-2px, -2px);
          }

          #{$item}-button {
            opacity: 1;
            transition: opacity 250ms;
            transition-delay: 120ms;
          }
        }
      }

      @include for-size(laptop-up) {
        &:first-of-type,
        &:nth-of-type(2) {
          width: calc(50% - 4.4rem - 19.58vw);
          margin: 0 4.4rem 2.65rem;
        }

        &:first-of-type {
          margin-right: 4.4rem;
          margin-left: 19.58vw;
        }

        &:nth-of-type(2) {
          margin-left: 4.4rem;
          margin-right: 19.58vw;
        }
      }

      &-name {
        @include font-size(xl);
        font-weight: 500;
        margin-top: 0.44rem;

        @include for-size(phone-only) {
          font-size: 0.75rem;
        }
      }

      &-position {
        @include font-size(xl);
        font-weight: 700;
        color: var(--color-orange);
        text-align: center;
        text-transform: uppercase;
        display: block;
        margin-bottom: 1rem;

        @include for-size(phone-only) {
          font-size: 0.75rem;
        }
      }

      &-popup {
        background: rgba(255, 255, 255, 0.9);

        @include for-size(phone-only) {
          padding: 10vw 1.88rem;
        }

        .popup {
          &__body {
            width: 62vw;
            margin: auto;
            background-color: transparent;
            box-shadow: none;

            @include for-size(tablet-down) {
              width: 100vw;
            }
          }

          &__close-button {
            color: white;

            @media (min-width: 1250px) {
              top: 4px;
            }

            @include for-size(phone-only) {
              top: -5px;
              right: -5px;
            }
          }
        }

        &-content-group {
          margin: 0;
          display: flex;

          @include for-size(tablet-down) {
            flex-direction: column;
          }

          &-text-wrapper {
            width: 36.2vw;
            max-width: 100%;
            background-color: var(--color-gray-300);
            padding: 1.9rem 3.34rem 3.125rem 3rem;
            margin-right: -16rem;
            text-align: left;
            transform: translateY(3.56rem);
            position: relative;
            z-index: 10;

            @include for-size(tablet-down) {
              order: 2;
              width: calc(100% - 5rem);
              transform: translateY(0);
              margin: -4rem auto 0;
            }

            @include for-size(phone-only) {
              padding: 1rem 1.25rem;
            }
          }

          &-title {
            font-size: 2.18rem;
            font-weight: 700;
            padding-right: 4.95rem;
            margin-bottom: 1.125rem;

            @include for-size(tablet-down) {
              font-size: 1.5rem;
              margin-bottom: 1rem;
            }

            @include for-size(phone-only) {
              font-size: 1.25rem;
              margin-bottom: 0.75rem;
            }
          }

          &-subtitle {
            color: var(--color-orange);
            font-size: 1.25rem;
            display: block;
            margin-bottom: 2.3rem;

            @include for-size(tablet-down) {
              font-size: 1rem;
              margin-bottom: 1rem;
            }

            @include for-size(phone-only) {
              font-size: 0.875rem;
              margin-bottom: 1rem;
            }
          }

          &-text {
            @include font-size(lg);
            line-height: 1.56rem;

            @include for-size(tablet-down) {
              font-size: 1.1rem;
              line-height: 1.2em;
            }

            @include for-size(phone-only) {
              font-size: 0.875rem;
              line-height: 1.2rem;
            }
          }

          &-button {
            font-size: 0;
            width: 20px;
            height: 20px;
            background-color: var(--color-blue-400);
            -webkit-mask-repeat: no-repeat;
            mask-repeat: no-repeat;
            -webkit-mask-position: left center;
            mask-position: left center;
            -webkit-mask-size: contain;
            mask-size: contain;
            -webkit-mask-image: url("assets/images/icon-linkedin.svg");
            mask-image: url("assets/images/icon-linkedin.svg");
            position: absolute;
            right: 1.75rem;
            top: 1.75rem;

            &:hover {
              opacity: 0.7;
            }
          }
        }

        &-media {
          width: 44.24vw;

          @include for-size(tablet-down) {
            width: 100%;
          }

          &-object {
            width: calc(100% - 5px);
            height: calc(100% - 5px);
            border-right: 5px solid var(--color-orange);
            border-bottom: 5px solid var(--color-orange);
          }
        }
      }
    }
  }
}
